import {
  Box,
  Center,
  Heading,
  Image,
  Link,
  Text,
  useColorModeValue,
} from "@chakra-ui/react";
import React from "react";

const Projects = () => {
  return (
    <Box
      id="projects"
      paddingTop="90px"
      backgroundColor={useColorModeValue("white", "#323946")}
      color={useColorModeValue("black", "white")}
    >
      <Box height={"90vh"}>
        <Heading as={"h2"}>Projects</Heading>
        <Center>
          <Box className="project-card">
            Project1
            <Image src="project-1" alt="project-1" />
            <Heading as={"h5"} className="project-title">
              Project Title
            </Heading>
            <Text className="project-description"> Project Description</Text>
            <Text className="project-tech-stack"> Project Tech Stack</Text>
            <Link className="project-deployed-link" href="#">
              Deployed
            </Link>
            <Link className="project-github-link" href="#">
              Github
            </Link>
          </Box>
          <Box className="project-card">
            Project2
            <Image src="project-2" alt="project-2" />
            <Heading as={"h5"} className="project-title">
              Project Title
            </Heading>
            <Text className="project-description"> Project Description</Text>
            <Text className="project-tech-stack"> Project Tech Stack</Text>
            <Link className="project-deployed-link" href="#">
              Deployed
            </Link>
            <Link className="project-github-link" href="#">
              Github
            </Link>
          </Box>
          <Box className="project-card">
            Project3
            <Image src="project-3" alt="project-3" />
            <Heading as={"h5"} className="project-title">
              Project Title
            </Heading>
            <Text className="project-description"> Project Description</Text>
            <Text className="project-tech-stack"> Project Tech Stack</Text>
            <Link className="project-deployed-link" href="#">
              Deployed
            </Link>
            <Link className="project-github-link" href="#">
              Github
            </Link>
          </Box>
          <Box className="project-card">
            Project4
            <Image src="project-4" alt="project-4" />
            <Heading as={"h5"} className="project-title">
              Project Title
            </Heading>
            <Text className="project-description"> Project Description</Text>
            <Text className="project-tech-stack"> Project Tech Stack</Text>
            <Link className="project-deployed-link" href="#">
              Deployed
            </Link>
            <Link className="project-github-link" href="#">
              Github
            </Link>
          </Box>
        </Center>
      </Box>
    </Box>
  );
};

export default Projects;
