import {
  Box,
  Heading,
  Image,
  SimpleGrid,
  Text,
  useColorModeValue,
} from "@chakra-ui/react";
import React from "react";
import Devgif from "./images/devgif.gif";

const About = () => {
  return (
    <Box
      id="about"
      className="about section"
      paddingTop="90px"
      class="about section"
      backgroundColor={useColorModeValue("white", "#323946")}
      color={useColorModeValue("black", "white")}
    >
      <Box height={"110vh"} width={"70vw"} margin={"auto"}>
        <Heading as={"h2"}>About me</Heading>
        <br />
        <SimpleGrid
          margin={"auto"}
          width={"100%"}
          columns={[1, 1, 1, 2]}
          gap={[10, 10, 10, 10]}
          alignItems={"center"}
        >
          <Box width={["80vw","18vw"]}>
            <Image
              src={Devgif}
              alt="Devgif-Pic"
              width={["65vw", "30vw"]}
              height={"50vh"}
              className="home-img"
            />
          </Box>
          <Box>
            <Text id="user-detail-intro" textAlign={"justify"}>
              An enthusiastic MERN Developer with excellent hands-on experience
              in developing scalable websites and applications using a wide
              range of front-end and back-end skills like HTML, CSS, JavaScript,
              React, Redux and Node Js. Highly skilled in the design,
              development and implementation of functional specifications, Ready
              for taking on challenging roles...
            </Text>
          </Box>
        </SimpleGrid>
      </Box>
    </Box>
  );
};

export default About;
