import {
  Box,
  Center,
  Heading,
  Image,
  Text,
  useColorModeValue,
} from "@chakra-ui/react";
import React from "react";
import htmlIcon from "./tech-stack/html.svg";
import cssIcon from "./tech-stack/css.svg";
import javascriptIcon from "./tech-stack/javascript.svg";
import reactIcon from "./tech-stack/react.svg";

const Skills = () => {
  return (
    <Box
      id="skills"
      paddingTop="90px"
      backgroundColor={useColorModeValue("white", "#323946")}
      color={useColorModeValue("black", "white")}
    >
      <Box height={"90vh"}>
        <Heading as={"h2"}>Skills</Heading>
        <Center>
          <Box className="skills-card">
            <Image src={htmlIcon} alt="html" className="skills-card-img" />
            <Text className="skills-card-name">HTML</Text>
          </Box>
          <Box className="skills-card">
            <Image src={cssIcon} alt="css" className="skills-card-img" />
            <Text className="skills-card-name">CSS</Text>
          </Box>
          <Box className="skills-card">
            <Image
              src={javascriptIcon}
              alt="javascript"
              className="skills-card-img"
            />
            <Text className="skills-card-name">JavaScript</Text>
          </Box>
          <Box className="skills-card">
            <Image src={reactIcon} alt="react" className="skills-card-img" />
            <Text className="skills-card-name">React Js</Text>
          </Box>
        </Center>
      </Box>
    </Box>
  );
};

export default Skills;
