import { Box, Link, useColorModeValue } from "@chakra-ui/react";
import React from "react";

const Contact = () => {
  return (
    <Box id="contact" paddingTop="90px" backgroundColor={useColorModeValue("white","#323946")} color={useColorModeValue("black","white")}>
      <Box height={"90vh"}>
        <h1>Contact</h1>
        <div style={{ display: "flex", justifyContent: "space-around" }}>
          <div>
            <Link href="https://github.com/Ajay84sia" id="contact-github" isExternal>
              GitHub
            </Link>
          </div>
          <div>
            <Link
              href="https://www.linkedin.com/in/ajay-84sia/"
              id="contact-linkedin"
              isExternal
            >
              LinkedIn
            </Link>
          </div>
          <div>
            <h1 id="contact-phone">+91 - 7011649635</h1>
          </div>
          <div>
            <h1 id="contact-email">Ajay9679339@gmail.com</h1>
          </div>
          <div></div>
        </div>
      </Box>
    </Box>
  );
};

export default Contact;
