import {
  Box,
  Button,
  Heading,
  Image,
  Link,
  SimpleGrid,
  Text,
  useColorModeValue,
} from "@chakra-ui/react";
import React from "react";
import Ajay_Resume from "./ResumeFolder/Ajay_Resume.pdf";
import ProfilPic from "./images/pic.JPG";

const Home = () => {
  return (
    <Box
      paddingTop="110px"
      id="home"
      backgroundColor={useColorModeValue("white", "#323946")}
      color={useColorModeValue("black", "white")}
    >
      <Box height={"110vh"} margin={"auto"} marginTop={"5%"} width={"70%"}>
        <SimpleGrid
          margin={"auto"}
          width={"100%"}
          columns={[1, 1, 1, 2]}
          gap={[10, 10, 10, 80]}
          alignItems={"center"}
        >
          <Box textAlign={"left"} width={"70vw"}>
            <Text fontSize="4xl" fontWeight="600" id="user-detail-intro">
              {" "}
              Hello, I'm
            </Text>
            <Heading fontSize="6xl" as={"h1"} id="user-detail-name">
              {" "}
              Ajay
            </Heading>
            <br />
            <Heading fontSize="4xl" as={"h1"} id="user-position">
              {" "}
              I'm a Full Stack Web Developer
            </Heading>
            <br />
            {/* <Text fontSize='xl'>
              A passionate full-stack web developer, who loves to buid cool
              projects and work in a team. I want to achieve global recognition
              and exposure with my skillset, mindset and toolset.
            </Text> */}

            <Link
              id="resume-button-2"
              href={Ajay_Resume}
              download="Ajay_Resume"
            >
              <Button
                onClick={() =>
                  window.open(
                    "https://drive.google.com/file/d/1FaGp-WVtNALfsCMVjZESGzTDR6q3SpRf/view?usp=sharing"
                  )
                }
              >
                Resume
              </Button>
            </Link>
          </Box>
          <Box width={["80vw","18vw"]}>
            <Image
              src={ProfilPic}
              alt="Profile-Pic"
              width={["65vw","30vw"]}
              height={"50vh"}
              boxShadow="rgb(7, 238, 255) 0px 0px 0px 3px"
              borderRadius={"30px"}
              className="home-img"
            />
          </Box>
        </SimpleGrid>
      </Box>
    </Box>
  );
};

export default Home;

